@use "../mixins" as *;
@use "../variables" as *;

.feedback {
  &__body {
    background-color: #F3F7FF;
    padding: 30px;
    border-radius: 20px;
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap:30px;
    position: relative;
  }
  &__column {
    flex-basis: 824px;
    flex-shrink: 0;
    @include vp-1199 {
      flex-basis: unset;
      flex-shrink: unset;
    }
  }
  &__image {
    height: 448px;
    border-radius: 20px;
    overflow: hidden;
    @include vp-1199 {
      display: none;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      max-height: 448px;
    }

  }
  &__form-body {
    flex-grow: 1;
  }
  &__input {
    flex-grow: 1;
  }
  &__textarea {
    margin-top: 20px;
    margin-bottom: 20px;
    @include vp-807 {
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }

  &__form-row {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @include vp-807 {
      flex-direction: column;
      gap: 10px;
    }
  }

  &__form-footer {
    display: flex;
    align-items: center;
    gap: 30px;
    margin-top: 30px;
    @include vp-807 {
      margin-top: 20px;
    }
    @include vp-615 {
      display: block;
    }
  }
  &__button {
    height: 51px;
    @include vp-615 {
      width: 100%;
      margin-bottom: 20px;
    }
  }
  &__agreement {
    max-width: 580px;
    font-size: 16px;
    color: $color-dark-gray;
    line-height: 140%;
    @include vp-807 {
      font-size: 14px;
    }
    a {
      color: $color-dark-gray;
      text-decoration: underline;
    }
  }
  &__title {
    font-family: "Montserrat";
    font-weight: 700;
    font-size: 25px;
    line-height: 130%;
    color: #000;
    margin: 0 0 20px 0;
    @include vp-1199 {
      font-size: 18px;
    }
  }
  &__text {
    padding-bottom: 15px;
  }

  &.is-success {
    .success {
      opacity: 1;
      pointer-events: auto;
    }
}


}

.success {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s ease;
  &__wrapper {
    flex-direction: column;
    padding: 30px 30px;
    border-radius: 20px;
    background-color: #f3f7ff;
    position: relative;
    width: 572px;
    @include vp-615 {
      width: 360px;
    }
  }
  &__text {
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: $color-default-black;
  }
  &__close-button {
    position: absolute;
    right: 10px;
    top: 10px;
  }

  h2 {
    font-family: "Montserrat";
    font-weight: 700;
    font-size: 20px;
    line-height: 140%;
    color: $color-default-black;
    margin: 20px 0 10px 0;
  }
}